.rozliseni {
  width: 500px;
  height: 1100px;
  background-color: rgb(28, 26, 26);
  background-image: url("../pictures/Quiz_Game/pixel_art.png");
  background-size: cover;
  background-position: center;
}

.ztmavit {
  background-color: rgba(0, 0, 0, 0.336);
  height: 100%;
}

.tophalf {
  height: 50%;
  background-color: rgba(0, 255, 255, 0);
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
}
.tophalf .nadpisText {
  display: inline-flex;
  margin: 0;
  color: rgb(255, 255, 255);
  font-size: 70px;
  font-weight: light;
  font-style: italic;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.bothalf {
  height: 50%;
  background-color: rgba(204, 255, 0, 0);
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
}
.bothalf .href {
  text-decoration: none;
}
.bothalf .startButt {
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  font-size: 70px;
  font-style: italic;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  width: 250px;
  height: 250px;
  border-radius: 100%;
  background-color: rgba(0, 0, 0, 0);
  color: white;
  border: 6px solid #ffffff; /* Border for visualization */
}
.bothalf .startButt .zeroMargin {
  margin: 0;
}

@keyframes animate {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}/*# sourceMappingURL=Uvod.css.map */